.logoContainer {
    margin-top: -5%;
}

.bigW {
    display: flex;
    justify-content: center;
    align-items: center;
}

.bigW h1 {
    color: #FDF8F6;
    font-size: 150px;
    backdrop-filter: blur(4px) brightness(90%); /*makes the background blurry*/
    padding: 0px 30px 0px 30px;
    border-radius: 20%;
    /*box-shadow: 3px 3px 50px 0px rgba(0, 0, 0, 0.4);*/
    box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 70px 0px, rgba(0, 0, 0, 0.06) 0px 0px 50px 0px;
}

.tip {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -90px;
}

.tip p {
    color: #FDF8F6;
    font-size: 22px;
    font-weight: bold;
    text-decoration-line: underline;
    text-decoration-color: #FDF8F6;
    text-decoration-thickness: 23%;
    text-underline-offset: 2px;
}
