.search-wrapper {
    width: 50%;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #E5E5E5; /*GREYER COLOR*/
    margin: 5px auto;
    padding: 0px 12px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
}

.search-input {
    height: 25px;
    width: 700px;
    font-size: 20px;
    border: none;
    outline: none;
    background: transparent;
}

.search-icon {
    background: transparent;
    border: none;
    outline: none;
}

.search-icon img {
  height: 25px;
  
}

