.navWhole {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.name {
  margin-left: 2%;
  color: #FDF8F6;
  font-size: 19px;
  text-decoration-line: underline;
  text-decoration-color: #FDF8F6;
  text-decoration-thickness: 3px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-right: 10%;
}

.buttons h3 {
  color: #FDF8F6;
  font-size: 20px;
  margin-top: 30px;
}

.buttons h3:hover {
  text-decoration-line: underline;
  text-decoration-color: #FDF8F6;
  text-decoration-thickness: 5px;
}

.buttons h3:first-child {
  margin-right: 70px;
}

a {
  color: #FDF8F6;
  text-decoration: none;
}

